.text-left {
  text-align: left;
}

.text-center {
  text-align: center;
}

.text-right {
  text-align: right;
}

.nowrap {
  white-space: nowrap;
}

.bold {
  font-weight: 700;
}

/* DEFAULT TITLES */
h1 {
  padding: 1rem 0;
  font-family: "Montserrat";
  font-size: 2rem;
  line-height: 1;
  font-weight: 600;
  color: #011D21;
}

h2 {
  padding: 1rem 0;
  font-family: "Montserrat";
  font-size: 2.5rem;
  line-height: 1;
  font-weight: 700;
  color: #011D21;
}
@media screen and (max-width: 768px) {
  h2 {
    font-size: 1.5rem;
  }
}

h3 {
  padding: 1rem 0;
  font-family: "Montserrat";
  font-size: 2.5rem;
  line-height: 1;
  font-weight: 700;
  color: #011D21;
  text-align: center;
}
@media screen and (max-width: 768px) {
  h3 {
    font-size: 1.5rem;
  }
}

h4 {
  padding: 1rem 0;
  font-family: "Montserrat";
  font-size: 1.5rem;
  line-height: 1;
  font-weight: 700;
  color: #101211;
}
@media screen and (max-width: 768px) {
  h4 {
    font-size: 1rem;
  }
}

h5 {
  padding: 1rem 0;
  font-family: "Montserrat";
  font-size: 1.2rem;
  line-height: 1;
  font-weight: 700;
  color: #011D21;
}

h6 {
  padding: 1rem 0;
  font-family: "Montserrat";
  font-size: 1rem;
  line-height: 1;
  font-weight: 700;
  color: #011D21;
}

/* DEFAULT PARAGRAPHS */
p {
  padding: 1rem 0;
  font-family: "Montserrat";
  font-size: 1rem;
  line-height: 1;
  font-weight: 400;
  color: #011D21;
}

ul, li, ol {
  list-style: none;
}

a {
  text-decoration: none;
  color: #011D21;
  outline: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  transition: none;
}
a:hover {
  color: #888;
  text-decoration: none;
}

