/*
CLEARFIX STYLE
 */
.clearfix::after,
.container::after,
.row::after {
  content: ".";
  display: block;
  height: 0;
  clear: both;
  visibility: hidden;
}

.canvas {
  width: 100%;
  height: 1000px;
  border: 1px solid #000;
}
.canvas canvas {
  width: 100%;
  height: 100%;
}